<template>
  <div id="excercisetracker-table">
      <h1>Use your inner Force</h1>
      <h2>Workout like a Jedi Knight</h2>
      <table>
      <thead>
          <tr>
              <th>Date</th>
              <th>Excercise</th>
              <th>Amount</th>
              <th>Actions</th>
          </tr>
      </thead>
      <tbody>
          <tr v-for="item in items" :key='item.id'>
              <td>{{item.date}}</td>
              <td>{{item.name}}</td>
              <td>{{item.amount}}</td>
              <td>
                  <button>Edit</button>
                  <button @click="$emit('delete:item', item.id)">Delete</button>
              </td>
          </tr>
      </tbody>    
      </table>
  </div>
</template>

<script>
export default {
    name: 'excercise-tracker',
    props: {
        items: Array
    }
}
</script>

